.backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.65);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  padding: 20px;
}

.modal {
  height: 80vh;
  width: 85vw;
  max-width: 960px;
  background: #111;
  border-radius: 12px;
  overflow: hidden;
  color: #fff; /* 글씨를 하얗게 */
}

.hero {
  height: 550px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.close {
  position: absolute;
  font-size: 16px;
  top: 12px;
  right: 12px;
  background-color: #111;
  color: #fff;
  border: 0;
  border-radius: 20px;
  padding: 7px 10px;
  cursor: pointer;
}

.content {
  padding: 18px 20px 22px;
}

.title {
  margin: 0 0 10px;
  font-size: 26px;
  font-weight: 800;
}

.overview {
  margin: 0;
  line-height: 1.6;
  opacity: 0.9;
}
